.bg {
  background-color: black;
}

.Introduction {
  margin: 0 auto;
  max-width: 1100px;
  text-align: center;
  background-color: bisque;
}

.appBar {
  display: flex;
  align-items: center;
  justify-content: space-around;
  background-color: bisque;
}

.appBar img {
  margin-top: -2em;
  height: 150px;
}

.links {
  display: flex;
  width: 250px;
  justify-content: space-evenly;
  background-color: rebeccapurple;
}

.hello {
  background-color: bisque;
  padding-bottom: 1em;
}

.sec {
  display: flex;
  margin-bottom: 1em;
}

.contento {
  width: 50%;
  background-color: green;
}

.tipos {
  margin: auto;
  width: 50%;
  height: 22em;
  background-color: hotpink;
  position: relative;
}

.cardo {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  align-items: center;
  justify-content: center;
  animation: slideFadeIn 1s ease-in-out;
}

@keyframes slideFadeIn {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

#prev,
#next {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  font-size: 2rem;
  color: black;
  text-decoration: none;
  background-color: rgb(0, 0, 0, 0.5);
  padding: 0.5rem;
  transition: all 0.3s ease-in-out;
}

#prev:hover,
#next:hover {
  background-color: rgba(0, 0, 0, 0.8);
}

#prev {
  left: 0;
}

#next {
  right: 0;
}

#quizo {
  display: none;
}
